<template>
  <div class="list-table">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12> </v-flex>
        <v-flex lg12>
          <v-card>
            <v-toolbar card color="white">
              <v-text-field
                flat
                solo
                prepend-icon="search"
                placeholder="Buscar"
                v-model="search"
                hide-details
                class="hidden-sm-and-down"
              ></v-text-field>
              <v-btn icon>
                <v-icon>filter_list</v-icon>
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn tile color="primary" dark @click="create">
                <v-icon left dark>add</v-icon>
                Novo
              </v-btn>
            </v-toolbar>
            <v-divider></v-divider>
            <v-card-text class="pa-0">
              <v-data-table
                :headers="headers"
                :loading="dataLoading"
                :search="search"
                :items="shares"
                :rows-per-page-items="[10, 25, 50, { text: 'All', value: -1 }]"
                class="elevation-1"
                item-key="id"
                select-all
                v-model="selected"
              >
                <template slot="items" slot-scope="props">
                  <td>
                    <v-checkbox primary hide-details v-model="props.selected"></v-checkbox>
                  </td>
                  <td>{{ props.item.id.substr(props.item.id.length - 5) }}</td>
                  <td>{{ props.item.level }}</td>
                  <td v-if="props.item.owner">{{ props.item.owner.fullName }}</td>
                  <td v-else>{{ null }}</td>

                  <td v-if="props.item.project">Projeto</td>
                  <td v-else-if="props.item.album">Album</td>
                  <td v-else-if="props.item.plan">Mapa</td>
                  <td v-else>{{ null }}</td>

                  <td v-if="props.item.project">
                    {{ props.item.project.id.substr(props.item.project.id.length - 5) }}
                  </td>
                  <td v-else-if="props.item.album">{{ props.item.album.id.substr(props.item.album.id.length - 5) }}</td>
                  <td v-else-if="props.item.plan">{{ props.item.plan.id.substr(props.item.plan.id.length - 5) }}</td>
                  <td v-else>{{ null }}</td>

                  <td v-if="props.item.user">{{ props.item.user.fullName }}</td>
                  <td v-else>{{ null }}</td>
                  <td>
                    <v-btn flat icon>
                      <v-icon color="primary" medium dark @click="view(props.item.id)">visibility</v-icon>
                    </v-btn>
                    <v-btn flat icon>
                      <v-icon color="secondary" medium dark @click="edit(props.item.id)">edit</v-icon>
                    </v-btn>
                    <v-btn flat icon>
                      <v-icon color="red" medium dark @click="confirmDeletion(props.item.id)">delete</v-icon>
                    </v-btn>
                  </td>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      dataLoading: false,
      selected: [],
      headers: [
        {
          text: "ID",
          value: "id"
        },
        {
          text: "Nível",
          value: "level"
        },
        {
          text: "Dono",
          value: "owner"
        },
        {
          text: "Tipo",
          value: "type"
        },
        {
          text: "Objeto",
          value: "shareObject"
        },
        {
          text: "Com",
          value: "user"
        },
        {
          text: "Ação",
          value: ""
        }
      ],
      shares: []
    }
  },
  mounted() {
    this.update()
  },
  computed: {},
  methods: {
    update() {
      this.dataLoading = true
      this.$store.dispatch("getShares").then(() => {
        this.shares = this.$store.getters.getShares
        this.dataLoading = false
      })
    },

    view(id) {
      this.$router.push({ name: "ViewShare", params: { shareId: id } })
    },

    create() {
      this.$router.push({ name: "NewShare" })
    },

    edit(id) {
      this.$router.push({ name: "EditShare", params: { shareId: id } })
    },

    async confirmDeletion(id) {
      const res = await this.$dialog.warning({
        text: "Você tem certeza disso? Esta decisão não pode ser desfeita.",
        title: "Remover"
      })
      if (res) {
        this.remove(id)
      }
    },

    remove(id) {
      this.$store
        .dispatch("deleteShare", id)
        .then(() => {
          this.$toast("Registro removido.", {
            color: "green",
            icon: "check_circle",
            dismissable: true,
            queueable: true
          })
        })
        .catch(e => {
          this.$toast("Não foi possível remover.", {
            color: "red",
            icon: "error",
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
    }
  }
}
</script>
