<template>
  <div id="page-text-fields">
    <album-details :album="album"></album-details>
  </div>
</template>

<script>
import AlbumDetails from "./AlbumDetails"

export default {
  props: {
    albumId: {
      type: String,
      default: null
    }
  },
  components: { AlbumDetails },
  data() {
    return {
      album: {
        id: null,
        name: "",
        description: "",
        altitude: 0,
        width: 0,
        camera: 0,
        height: 0,
        type: "",
        count: 0,
        owner: null
      },
      listUsers: []
    }
  },
  mounted() {
    if (this.albumId) {
      this.get(this.albumId)
    }
  },
  computed: {},
  methods: {
    get(id) {
      this.$store.dispatch("getAlbum", id).then(album => {
        this.album = album
        this.album.owner = this.album.owner.fullName
      })
    }
  }
}
</script>
