<template>
  <div><plan-details :plan="plan"></plan-details></div>
</template>

<script>
import PlanDetails from "./PlanDetails"
export default {
  props: {
    planId: {
      type: String,
      default: null
    }
  },
  components: { PlanDetails },
  data() {
    return {
      plan: {
        id: null,
        album: "",
        location: "",
        owner: "",
        project: ""
      }
    }
  },
  mounted() {
    if (this.planId) {
      this.get(this.planId)
    }
  },
  methods: {
    get(id) {
      this.$store.dispatch("getPlan", id).then(plan => {
        this.plan = plan
        if (plan.owner.fullName) {
          this.plan.owner = plan.owner.fullName
        } else {
          this.plan.owner = plan.owner
        }
      })
    }
  }
}
</script>

