<template>
  <div id="page-text-fields">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-container>
              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Dono</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listUsers"
                    v-model="user"
                    label="Selecione"
                    v-validate="'required'"
                    @change="onChangeUser"
                    :error-messages="errors.collect('user')"
                    item-text="fullName"
                    item-value="id"
                    data-vv-name="user"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Tipo</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="['orthophoto', 'dsm','dtm']"
                    v-model="orthomosaic.type"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('type')"
                    data-vv-name="type"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Url dos tiles</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="orthomosaic.tilesUrl"
                    v-validate="'required'"
                    :error-messages="errors.collect('tilesUrl')"
                    label="Url"
                    data-vv-name="tilesUrl"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Mapa</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listPlans"
                    v-model="orthomosaic.plan"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('plan')"
                    item-text="id"
                    item-value="id"
                    data-vv-name="plan"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Imagem</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listImages"
                    v-model="orthomosaic.image"
                    label="Selecione"
                    :error-messages="errors.collect('image')"
                    item-text="id"
                    item-value="id"
                    data-vv-name="image"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Tarefa</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listTasks"
                    v-model="orthomosaic.task"
                    label="Selecione"
                    :error-messages="errors.collect('task')"
                    item-text="id"
                    item-value="id"
                    data-vv-name="task"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-spacer></v-spacer>

                <v-btn @click="clear">Limpar</v-btn>
                <v-btn class="mr-4" @click="validateForm" color="primary">Salvar</v-btn>
              </v-layout>
                
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
  
<script>

export default {
  props: {
    orthomosaicId: {
      type: String,
      default: null
    },
  },
  data() {
    return {
      user: null,
      orthomosaic: {
        id: null,
        tilesUrl: '',
        type: '',
        plan: null,
        image: null,
        task: null
      },
      listUsers: [],
      listPlans: [],
      listTasks: [],
      listImages: [],
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })
    this.$store.dispatch('getTasks')
    .then(() => {
      this.listTasks = this.$store.getters.getTasks
    })

    if (this.orthomosaicId){
      this.update(this.orthomosaicId)
    }
  },
  computed: {},
  methods: {
    onChangeUser: function () {
      this.$store.dispatch('getPlans', 'owner=' + this.user)
      .then(() => {
        this.listPlans = this.$store.getters.getPlans
      })
      this.$store.dispatch('getImages')
      .then(() => {
        this.listImages = this.$store.getters.getImages
      })
    },

    update(id) {
      this.$store.dispatch('getOrthomosaic', id)
      .then((orthomosaic) => {
        this.orthomosaic = orthomosaic
        if (this.orthomosaic.plan) {
          this.user = this.orthomosaic.plan.owner
          this.orthomosaic.plan = this.orthomosaic.plan.id
          this.orthomosaic.image = this.orthomosaic.image.id
          this.orthomosaic.task = this.orthomosaic.task.id
        }
        this.onChangeUser()

      })
    },

    validateForm() {
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.save()
        }
      })
    },

    save() {
     if (this.orthomosaic.id == null) {
        this.$store.dispatch('postOrthomosaic', this.orthomosaic)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro adicionado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível adicionar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      } else {
        this.$store.dispatch('editOrthomosaic', this.orthomosaic)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro editado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível editar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      }
    },

    clear() {
      this.user = null
      this.orthomosaic.tilesUrl = ''
      this.orthomosaic.type = ''
      this.orthomosaic.plan = null
      this.orthomosaic.image = null
      this.orthomosaic.task = null
    }
  }
}
</script>
