<template>
  <v-expansion-panel>
    <v-expansion-panel-content>
      <template v-slot:header>
        <div>Visualizar Projetos ({{ projects.length }})</div>
      </template>
      <v-card>
        <v-flex sm12>
          <v-dialog v-model="dialog">
            <template v-slot:activator="{ on }">
              <v-card>
                <div v-for="(project, index) in projects" :key="'project-' + index">
                  <v-card>
                    <v-card-title @click="showModalData(project)" v-on="on" class="card-description-text"
                      >Projeto: &nbsp
                      <a>{{ `${project.name} - ${project._id.substr(project._id.length - 5)}` }}</a>
                    </v-card-title>
                  </v-card>
                </div>
              </v-card>
            </template>

            <v-card>
              <project-details :project="clickedProject[clickedProject.length - 1]"></project-details>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" text @click=";(dialog = false), (closeModalData = true)">
                  Sair
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-card>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>
<script>
import ProjectDetails from "../project/ProjectDetails"

export default {
  // eslint-disable-next-line vue/name-property-casing
  name: "projects-expansion-list",
  props: {
    projects: {
      type: Array,
      default: null
    }
  },
  components: { ProjectDetails },

  data() {
    return {
      clickedProject: [],
      dialog: false,
      showData: false
    }
  },
  mounted() {
    if (this.userId) {
      this.get(this.userId)
    }
  },
  computed: {},
  methods: {
    showModalData(project) {
      this.showData = true
      if (project.owner.fullName) {
        project.owner = project.owner.fullName
      }
      this.clickedProject.push(project)
    },
    closeModalData() {
      ;(this.showModalData = false), (this.dialog = false)
      this.clickedProject.pop()
    },
    showProjectDetails() {
      this.showProject = true
    }
  }
}
</script>
