<template>
  <div id>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex sm6>
          <v-card class="card-info-style">
            <v-card-title class="card-title">Descrição</v-card-title>
            <v-card-text class="card-description-text">
              <h3></h3>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex sm6>
          <v-card class="card-info-style">
            <v-card-title class="card-title">Informações</v-card-title>
            <v-card-text>
              Compartilhamento:
              <div v-if="share.project">{{ share.project.name }}</div>
              <div v-else-if="share.album">{{ share.album.name }}</div>
              <div v-else-if="share.plan">{{ share.plan.name }}</div>
            </v-card-text>
            <v-card-text>Nível: {{ share.level }}</v-card-text>
            <v-card-text v-if="share.linkSharing"
              >Link de compartilhamento: <a :href="`${share._id}`">{{ `${share._id}` }}</a></v-card-text
            >
            <v-card-text v-else>Compartilhado com: {{ share.user.fullName }}</v-card-text>
            <v-card-text>Dono do álbum: {{ share.owner }}</v-card-text>
          </v-card>
        </v-flex>
        <v-flex sm12
          ><v-dialog v-model="dialog">
            <template v-slot:activator="{ on }">
              <div v-if="share.project">
                <v-btn color="primary" @click="showProjectDetails()" v-on="on"> Visualizar Projeto </v-btn>
              </div>
              <div v-if="share.album">
                <v-btn color="primary" @click="showAlbumDetails()" dark v-on="on"> Visualizar Álbum </v-btn>
              </div>
              <div v-if="share.plan">
                <v-btn color="primary" @click="showPlanDetails()" dark v-on="on"> Visualizar Mapa </v-btn>
              </div>
            </template>

            <v-card>
              <album-details v-if="showAlbum" :album="share.album"></album-details>
              <plan-details v-if="showPlan" :plan="share.plan"></plan-details>
              <project-details v-if="showProject" :project="share.project"></project-details>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" flat @click="dialog = false">
                  Sair
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import AlbumDetails from "../album/AlbumDetails"
import PlanDetails from "../plan/PlanDetails"
import ProjectDetails from "../project/ProjectDetails"

export default {
  props: {
    shareId: {
      type: String,
      default: null
    }
  },
  components: { AlbumDetails, PlanDetails, ProjectDetails },

  data() {
    return {
      showAlbum: false,
      showProject: false,
      showPlan: false,

      dialog: false,
      search: "",
      dataLoading: false,
      selected: [],
      headers: [
        {
          text: "ID",
          value: "id"
        },
        {
          text: "Nível",
          value: "level"
        },
        {
          text: "Dono",
          value: "owner"
        },
        {
          text: "Tipo",
          value: "type"
        },
        {
          text: "Objeto",
          value: "shareObject"
        },
        {
          text: "Com",
          value: "user"
        },
        {
          text: "Ação",
          value: ""
        }
      ],
      share: null
    }
  },
  mounted() {
    this.get(this.shareId)
  },
  computed: {},
  methods: {
    get(id) {
      this.dataLoading = true
      this.$store.dispatch("getShare", id).then(share => {
        this.share = share
        this.share.owner = this.share.owner.fullName
        this.dataLoading = false
        console.log(share)
        if (share.plan) {
          this.$store.dispatch("getPlan", share.plan._id).then(plan => {
            this.share.plan = plan
            if (plan.owner.fullName) {
              this.share.plan.owner = plan.owner.fullName
            } else {
              this.share.plan.owner = plan.owner
            }
          })
        }
        if (share.album) {
          this.$store.dispatch("getAlbum", share.album._id).then(album => {
            this.share.album = album
            if (album.owner.fullName) {
              this.share.album.owner = album.owner.fullName
            } else {
              this.share.album.owner = album.owner
            }
          })
        }
        if (share.project) {
          this.$store.dispatch("getProject", share.project._id).then(project => {
            this.share.project = project
            if (project.owner.fullName) {
              this.share.project.owner = project.owner.fullName
            } else {
              this.share.project.owner = project.owner
            }
          })
        }
      })
    },
    showAlbumDetails() {
      this.showAlbum = true
      this.showPlan = false
      this.showProject = false
    },
    showPlanDetails() {
      this.showPlan = true
      this.showProject = false
      this.showAlbum = false
    },
    showProjectDetails() {
      this.showProject = true
      this.showAlbum = false
      this.showPlan = false
    }
  }
}
</script>
<style lang="scss">
.card-info-style {
  text-align: center;
  padding: 1%;
  margin: 1%;
  height: 25rem;
  width: 100%;
}
.card-title {
  font-size: 2rem;
}
</style>
