/* eslint-disable vue/name-property-casing */
<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-card-title class="card-title">Mapa - {{ plan.name }}</v-card-title>

            <v-card-text>Dono: {{ plan.owner }}</v-card-text>

            <v-card-text>Localização: {{ plan.location ? plan.location : "Localização não informada" }}</v-card-text>
          </v-card>
        </v-flex>
        <v-flex sm12>
          <v-dialog v-model="dialog">
            <template v-slot:activator="{ on }">
              <v-card>
                <v-card v-if="plan.album">
                  <v-card-title @click="get(plan.album._id)" v-on="on" class="card-description-text"
                    >Álbum: &nbsp <a>{{ plan.album.name ? plan.album.name : "Álbum não tem nome" }}</a>
                  </v-card-title>
                </v-card>
                <v-card v-else>
                  <v-card-text>Mapa não possui álbum</v-card-text>
                </v-card>
              </v-card>
            </template>
            <v-card>
              <v-btn color="primary" text @click="dialog = false">
                Sair
              </v-btn>
            </v-card>

            <v-card v-if="this.plan.album">
              <album-details :album="this.album"></album-details>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import AlbumDetails from "../album/AlbumDetails.vue"
export default {
  // eslint-disable-next-line vue/name-property-casing
  name: "plan-details",
  props: {
    plan: {
      type: Object,
      default: null
    }
  },
  components: { AlbumDetails },
  data() {
    return {
      dialog: false,
      album: {
        id: null,
        name: "",
        description: "",
        altitude: 0,
        width: 0,
        camera: 0,
        height: 0,
        type: "",
        count: 0,
        owner: null
      },
      listUsers: []
    }
  },
  methods: {
    closeModalData() {
      this.showModalData = false
      this.dialog = false
    },
    get(id) {
      this.$store.dispatch("getAlbum", id).then(album => {
        this.album = album
        if (album.owner.fullName) {
          this.album.owner = album.owner.fullName
        } else {
          this.album.owner = album.owner
        }
      })
    }
  }
}
</script>

<style lang="scss">
.card-title {
  font-size: 2rem;
}
.card-description-text {
  font-size: 1.3rem;
}
</style>
