<template>
  <v-expansion-panel>
    <v-expansion-panel-content>
      <template v-slot:header>
        <div>Visualizar Mapas ({{ plans.length }})</div>
      </template>
      <v-card>
        <v-flex sm12>
          <v-dialog v-model="dialog">
            <template v-slot:activator="{ on }">
              <v-card>
                <div v-for="(plan, index) in plans" :key="'plan-' + index">
                  <v-card>
                    <v-card-title @click="showModalData(plan)" v-on="on" class="card-description-text"
                      >Mapa: &nbsp <a>{{ `${plan.name} - ${plan._id.substr(plan._id.length - 5)}` }}</a>
                    </v-card-title>
                  </v-card>
                </div>
              </v-card>
            </template>

            <v-card>
              <plan-details :plan="clickedPlan[clickedPlan.length - 1]"></plan-details>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" text @click=";(dialog = false), (closeModalData = true)">
                  Sair
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-card>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>
<script>
import PlanDetails from "../plan/PlanDetails"

export default {
  // eslint-disable-next-line vue/name-property-casing
  name: "plans-expansion-list",
  props: {
    plans: {
      type: Array,
      default: null
    }
  },
  components: { PlanDetails },

  data() {
    return {
      clickedPlan: [],
      showData: false,
      dialog: false
    }
  },
  mounted() {
    if (this.userId) {
      this.get(this.userId)
    }
  },
  computed: {},
  methods: {
    showModalData(plan) {
      this.showData = true
      if (plan.owner.fullName) {
        plan.owner = plan.owner.fullName
      }
      this.clickedPlan.push(plan)
    },
    closeModalData() {
      ;(this.showModalData = false), (this.dialog = false)
      this.clickedPlan.pop()
    },
    showPlanDetails() {
      this.showPlan = true
    }
  }
}
</script>
