<template>
  <div id="page-text-fields">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-container>
              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Nome</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="album.name"
                    v-validate="'required'"
                    :error-messages="errors.collect('name')"
                    label="Nome"
                    data-vv-name="name"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Descrição</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-textarea
                    v-model="album.description"
                    :error-messages="errors.collect('description')"
                    label="Descrição"
                    data-vv-name="description"
                    required
                  ></v-textarea>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Altitude</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="album.altitude"
                    type="number"
                    v-validate="'required|min_value:1'"
                    :error-messages="errors.collect('altitude')"
                    label="Altitude"
                    data-vv-name="altitude"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Dimensões</v-subheader>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    v-model="album.width"
                    v-validate="'required|min_value:1'"
                    :error-messages="errors.collect('width')"
                    label="Largura (width)"
                    type="number"
                    data-vv-name="width"
                    required
                  ></v-text-field>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    v-model="album.height"
                    type="number"
                    v-validate="'required|min_value:1'"
                    :error-messages="errors.collect('height')"
                    label="Altura (height)"
                    data-vv-name="height"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Tipo</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    label="Tipo"
                    :items="['Rural', 'Urbano', 'Florestal', 'Minas']"
                    v-model="album.type"
                    v-validate="'required'"
                    data-vv-name="type"
                    :error-messages="errors.collect('type')"
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Camera</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    label="Tipo"
                    :items="['RGB', 'Multispectral', 'Calor']"
                    v-model="album.camera"
                    v-validate="'required'"
                    data-vv-name="camera"
                    :error-messages="errors.collect('camera')"
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Dono</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listUsers"
                    v-model="album.owner"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('owner')"
                    item-text="fullName"
                    item-value="id"
                    data-vv-name="owner"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-spacer></v-spacer>

                <v-btn @click="clear">Limpar</v-btn>
                <v-btn class="mr-4" @click="validateForm" color="primary">Salvar</v-btn>
              </v-layout>
                
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
  
<script>

export default {
  props: {
    albumId: {
      type: String,
      default: null
    },
  },
  data() {
    return {
      album: {
        id: null,
        name: '',
        description: '',
        altitude: 0,
        width: 0,
        camera: 0,
        height: 0,
        type: '',
        count: 0,
        owner: null
      },
      listUsers: [],
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })

    if (this.albumId){
      this.update(this.albumId)
    }
  },
  computed: {},
  methods: {
    update(id) {
      this.$store.dispatch('getAlbum', id)
      .then((album) => {
        this.album = album
        this.album.owner = this.album.owner.id
        delete this.album.images
      })
    },

    validateForm() {
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.save()
        }
      })
    },

    save() {
     if (this.album.id == null) {
        this.$store.dispatch('postAlbum', this.album)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro adicionado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível adicionar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      } else {
        this.$store.dispatch('editAlbum', this.album)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro editado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível editar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      }
    },

    clear() {
      this.album.name = ''
      this.album.description = ''
      this.album.altitude = 0
      this.album.width = 0
      this.album.height = 0
      this.album.type = ''
      this.album.count = 0
      this.album.owner = null
    }
  }
}
</script>
