<template>
  <div id="page-text-fields">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-container>
              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Nome do arquivo</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="image.filename"
                    v-validate="'required'"
                    :error-messages="errors.collect('filename')"
                    label="Nome do arquivo"
                    data-vv-name="filename"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Size</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="image.size"
                    :error-messages="errors.collect('size')"
                    label="Tamanho"
                    v-validate="'required'"
                    data-vv-name="size"
                    required
                    type="number"
                    suffix="bytes"
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Resolução</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="image.resolution"
                    type="number"
                    v-validate="'required|min_value:1'"
                    :error-messages="errors.collect('resolution')"
                    label="Resolução"
                    data-vv-name="resolution"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Dimensões</v-subheader>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    v-model="image.width"
                    v-validate="'required|min_value:1'"
                    :error-messages="errors.collect('width')"
                    type="number"
                    label="Largura (width)"
                    data-vv-name="width"
                    required
                  ></v-text-field>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    v-model="image.height"
                    type="number"
                    v-validate="'required|min_value:1'"
                    :error-messages="errors.collect('height')"
                    label="Altura (height)"
                    data-vv-name="height"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Tipo do arquivo</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="image.type"
                    v-validate="'required'"
                    :error-messages="errors.collect('type')"
                    label="Tipo do arquivo"
                    data-vv-name="type"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Url</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    label="Url"
                    v-model="image.url"
                    v-validate="'required'"
                    data-vv-name="url"
                    :error-messages="errors.collect('url')"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Url do Thumbnail</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    label="Url do Thumbnail"
                    v-model="image.thumbnail_url"
                    v-validate="'required'"
                    data-vv-name="thumbnail_url"
                    :error-messages="errors.collect('thumbnail_url')"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Dono</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listUsers"
                    v-model="user"
                    label="Selecione"
                    v-validate="'required'"
                    @change="onChangeUser"
                    :error-messages="errors.collect('user')"
                    item-text="fullName"
                    item-value="id"
                    data-vv-name="user"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Album</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listAlbums"
                    v-model="image.album"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('album')"
                    item-text="id"
                    item-value="id"
                    data-vv-name="album"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-spacer></v-spacer>

                <v-btn @click="clear">Limpar</v-btn>
                <v-btn class="mr-4" @click="validateForm" color="primary">Salvar</v-btn>
              </v-layout>
                
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
  
<script>

export default {
  props: {
    imageId: {
      type: String,
      default: null
    },
  },
  data() {
    return {
      user: null,
      image: {
        id: null,
        filename: '',
        size: '',
        resolution: 0,
        width: 0,
        height: 0,
        type: '',
        url: '',
        location: 0,
        thumbnail_url: '',
        album: null
      },
      listUsers: [],
      listAlbums: [],
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })

    if (this.imageId){
      this.update(this.imageId)
    }
  },
  computed: {},
  methods: {
    onChangeUser: function () {
      this.$store.dispatch('getAlbums', 'owner=' + this.user)
      .then(() => {
        this.listAlbums = this.$store.getters.getAlbums
      })
    },

    update(id) {
      this.$store.dispatch('getImage', id)
      .then((image) => {
        if (image.album){
          this.user = image.album.owner
          this.image.album = image.album.id
        }
        this.image = image

      this.onChangeUser()
      })
    },

    validateForm() {
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.save()
        }
      })
    },

    save() {
     if (this.image.id == null) {
        this.$store.dispatch('postImage', this.image)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro adicionado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível adicionar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      } else {
        this.$store.dispatch('editImage', this.image)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro editado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível editar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      }
    },

    clear() {
      this.image.filename = ''
      this.image.size = ''
      this.image.resolution = 0
      this.image.width = 0
      this.image.height = 0
      this.image.type = 0
      this.image.url = ''
      this.image.location = 0
      this.image.thumbnail_url = ''
      this.image.album = null
    }
  }
}
</script>
