<template>
  <div>
    <orthomosaic-details :orthomosaic="orthomosaic"></orthomosaic-details>
  </div>
</template>
<script>
import OrthomosaicDetails from "./OrthomosaicDetails"
export default {
  props: {
    orthomosaicId: {
      type: String,
      default: null
    }
  },
  components: {
    OrthomosaicDetails
  },
  data() {
    return {
      orthomosaic: {
        _id: null,
        plan: "",
        type: "",
        task: "",
        tilesUrl: "",
        image: "",
        geometrys: ""
      },
      listUsers: []
    }
  },
  mounted() {
    if (this.orthomosaicId) {
      this.get(this.orthomosaicId)
    }
  },
  computed: {},
  methods: {
    get(id) {
      this.$store.dispatch("getOrthomosaic", id).then(orthomosaic => {
        this.orthomosaic = orthomosaic
        console.log(orthomosaic)
      })
    }
  }
}
</script>
<style scoped></style>
