<template>
  <div id="page-text-fields">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-container>
              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Dono</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listUsers"
                    v-model="project.owner"
                    label="Selecione"
                    v-validate="'required'"
                    @change="onChangeUser"
                    :error-messages="errors.collect('user')"
                    item-text="fullName"
                    item-value="id"
                    data-vv-name="user"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="project.owner">
                <v-flex xs4>
                  <v-subheader>Nome</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="project.name"
                    v-validate="'required'"
                    :error-messages="errors.collect('name')"
                    label="Nome"
                    data-vv-name="name"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row v-if="project.owner">
                <v-flex xs4>
                  <v-subheader>Descrição</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-textarea
                    v-model="project.description"
                    :error-messages="errors.collect('description')"
                    label="Descrição"
                    data-vv-name="description"
                    required
                  ></v-textarea>
                </v-flex>
              </v-layout>

              <v-layout row v-if="project.owner">
                <v-flex xs4>
                  <v-subheader>Localização</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="project.location"
                    :error-messages="errors.collect('location')"
                    label="Localização"
                    data-vv-name="location"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-spacer></v-spacer>

                <v-btn @click="clear">Limpar</v-btn>
                <v-btn class="mr-4" @click="validateForm" color="primary">Salvar</v-btn>
              </v-layout>
                
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
  
<script>

export default {
  props: {
    projectId: {
      type: String,
      default: null
    },
  },
  data() {
    return {
      project: {
        id: null,
        name: '',
        description: '',
        location: null,
        owner: null,
      },
      listUsers: [],
      listPlans: [],
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })

    if (this.projectId){
      this.update(this.projectId)
    }
  },
  computed: {},
  methods: {
    onChangeUser: function () {
      this.$store.dispatch('getPlans', 'owner=' + this.project.owner)
      .then(() => {
        this.listPlans = this.$store.getters.getPlans
      })
    },

    update(id) {
      this.$store.dispatch('getProject', id)
      .then((project) => {
        this.project = project
        this.project.owner = this.project.owner.id
        this.onChangeUser()
      })
    },

    validateForm() {
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.save()
        }
      })
    },

    save() {
     if (this.project.id == null) {
        this.$store.dispatch('postProject', this.project)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro adicionado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível adicionar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      } else {
        this.$store.dispatch('editProject', this.project)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro editado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível editar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      }
    },

    clear() {
      this.project.name = ''
      this.project.description = ''
      this.project.location = null
      this.project.owner = null
    }
  }
}
</script>
