<template>
  <div id="page-text-fields">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-container>
              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Dono</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listUsers"
                    v-model="user"
                    label="Selecione"
                    v-validate="'required'"
                    @change="onChangeUser"
                    :error-messages="errors.collect('user')"
                    item-text="fullName"
                    item-value="id"
                    data-vv-name="user"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Status</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="['waiting', 'processing', 'tiling', 'completed']"
                    v-model="task.status"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('status')"
                    data-vv-name="status"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Tarefa</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="['tiles', 'orthomosaic']"
                    v-model="task.job"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('job')"
                    data-vv-name="job"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Mapa</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listPlans"
                    v-model="task.plan"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('plan')"
                    item-text="id"
                    item-value="id"
                    data-vv-name="plan"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Album</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listAlbums"
                    v-model="task.album"
                    label="Selecione"
                    :error-messages="errors.collect('album')"
                    item-text="id"
                    item-value="id"
                    data-vv-name="album"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="user">
                <v-flex xs4>
                  <v-subheader>Ortomosaico</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listOrthomosaics"
                    v-model="task.orthomosaic"
                    label="Selecione"
                    :error-messages="errors.collect('orthomosaic')"
                    item-text="id"
                    item-value="id"
                    data-vv-name="orthomosaic"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-spacer></v-spacer>

                <v-btn @click="clear">Limpar</v-btn>
                <v-btn class="mr-4" @click="validateForm" color="primary">Salvar</v-btn>
              </v-layout>
                
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
  
<script>

export default {
  props: {
    taskId: {
      type: String,
      default: null
    },
  },
  data() {
    return {
      user: null,
      task: {
        id: null,
        status: '',
        job: '',
        instance: null,
        album: null,
        orthomosaic: null,
        plan: null
      },
      listUsers: [],
      listInstances: [],
      listPlans: [],
      listAlbums: [],
      listOrthomosaics: [],
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })

    if (this.taskId){
      this.update(this.taskId)
    }
  },
  computed: {},
  methods: {
    onChangeUser: function () {
      this.$store.dispatch('getPlans', 'owner=' + this.user)
      .then(() => {
        this.listPlans = this.$store.getters.getPlans
      })
      this.$store.dispatch('getAlbums', 'owner=' + this.user)
      .then(() => {
        this.listAlbums = this.$store.getters.getAlbums
      })
      this.$store.dispatch('getOrthomosaics')
      .then(() => {
        this.listOrthomosaics = this.$store.getters.getOrthomosaics
      })
    },

    update(id) {
      this.$store.dispatch('getTask', id)
      .then((task) => {
        this.task = task
        if (this.task.plan) {
          this.user = this.task.plan.owner.id
          this.task.plan = this.task.plan.id
          this.onChangeUser()
          this.task.album = this.task.album.id
          this.task.orthomosaic = this.task.orthomosaic.id
        }

      })
    },

    validateForm() {
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.save()
        }
      })
    },

    save() {
     if (this.task.id == null) {
        this.$store.dispatch('postTask', this.task)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro adicionado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível adicionar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      } else {
        this.$store.dispatch('editTask', this.task)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro editado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível editar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      }
    },

    clear() {
      this.user = null
      this.task.tilesUrl = ''
      this.task.type = ''
      this.task.plan = null
      this.task.image = null
      this.task.task = null
    }
  }
}
</script>
