<template>
  <div class="list-table">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          
        </v-flex>
        <v-flex lg12>
          <v-card>
            <v-toolbar card color="white">
              <v-text-field
                flat
                solo
                prepend-icon="search"
                placeholder="Buscar"
                v-model="search"
                hide-details
                class="hidden-sm-and-down"
              ></v-text-field>
              <v-btn icon>
                <v-icon>filter_list</v-icon>
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn tile color="primary" dark @click="create">
                <v-icon left dark>add</v-icon>
                Novo
              </v-btn>
            </v-toolbar>
            <v-divider></v-divider>
            <v-card-text class="pa-0">
              <v-data-table
                :headers="headers"
                :loading="dataLoading"
                :search="search"
                :items="tasks"
                :rows-per-page-items="[10, 25, 50, { text: 'All', value: -1 }]"
                class="elevation-1"
                item-key="id"
                select-all
                v-model="selected"
              >
                <template slot="items" slot-scope="props" >
                  <td>
                    <v-checkbox primary hide-details v-model="props.selected"></v-checkbox>
                  </td>
                  <td>{{ props.item.id.substr(props.item.id.length - 5) }}</td>

                  <td v-if="props.item.orthomosaic">{{ props.item.orthomosaic.id.substr(props.item.orthomosaic.id.length - 5) }}</td>
                  <td v-else>{{ null }}</td>

                  <td v-if="props.item.album">{{ props.item.album.id.substr(props.item.album.id.length - 5) }}</td>
                  <td v-else>{{ null }}</td>

                  <td v-if="props.item.plan">{{ props.item.plan.id.substr(props.item.plan.id.length - 5) }}</td>
                  <td v-else>{{ null }}</td>

                  <td v-if="props.item.job">{{ props.item.job }}</td>
                  <td v-else>{{ null }}</td>

                  <td v-if="props.item.error">{{ props.item.error }}</td>
                  <td v-else>{{ null }}</td>

                  <td v-if="props.item.status">{{ props.item.status }}</td>
                  <td v-else>{{ null }}</td>

                  <td>
                                        

                    <v-dialog v-model="dialogNote[props.item.id]" max-width="600px" :key="props.item.id">
                      <template v-slot:activator="{ on }">
                        <v-btn icon v-on="on">
                          <v-icon color="primary" medium dark>play_arrow</v-icon>
                        </v-btn>
                      </template>
                      <v-card>
                        <v-card-title>
                          <span class="headline">Executar tarefa</span>
                        </v-card-title>
                        <v-card-text>
                          <v-container grid-list-md>
                            <v-layout wrap>
                              
                              <v-flex xs12>
                                <v-text-field label="Cores" v-model="instance.cores" required></v-text-field>
                              </v-flex>
                              <v-flex xs12>
                                <v-text-field label="Memória" v-model="instance.mem" required></v-text-field>
                              </v-flex>
                              <v-flex xs12>
                                <v-text-field label="Disco" v-model="instance.disk" required></v-text-field>
                              </v-flex>
                              <v-flex xs12>
                                <v-select
                                  :items="['projeto-233719', 'fine-entry-221919']"
                                  label="Projeto"
                                  v-model="instance.project"
                                  item-text="name"
                                  item-value="id"
                                  required
                                ></v-select>
                              </v-flex>
                            </v-layout>
                          </v-container>
                        </v-card-text>
                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn text @click.stop="$set(dialogNote, props.item.id, false)">Fechar</v-btn>
                          <v-btn color="primary" text @click="execTask(props.item.id)">Executar</v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>

                    <v-btn flat icon>
                      <v-icon color="primary" medium dark @click="view(props.item.id)">visibility</v-icon>
                    </v-btn>     
                    <v-btn flat icon>
                      <v-icon color="secondary" medium dark @click="edit(props.item.id)">edit</v-icon>
                    </v-btn>  
                    <v-btn flat icon>
                      <v-icon color="red" medium dark @click="confirmDeletion(props.item.id)">delete</v-icon>
                    </v-btn>
                  </td>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>

export default {
  data() {
    return {
      dialogNote: {},
      dataLoading: true,
      instance: {
        id: null,
        cores: '',
        mem: '',
        disk: '',
        project: ''
      },
      search: "",
      selected: [],
      headers: [
        {
          text: "ID",
          value: "id"
        },
        {
          text: "Ortomosaico",
          value: "orthomosaic"
        },
        {
          text: "Album",
          value: "album"
        },
        {
          text: "Plan",
          value: "plan"
        },
        {
          text: "Tarefa",
          value: "job"
        },
        {
          text: "Erro",
          value: "error"
        },
        {
          text: "Status",
          value: "status"
        },
        {
          text: "Ação",
          value: ""
        }
      ],
      tasks: []
    }
  },
  mounted () {
    this.update()
  },
  methods: {
    execTask (id) {
      this.$store.dispatch('postRun', {taskId: id, instance: this.instance})
      .then(() => {
        this.$toast('Tarefa executada.', {
          color: 'green',
          icon: 'check_circle',
          dismissable: true,
          queueable: true
        })
        this.dialogNote[id] = false
      })
      .catch((e) => {
        this.$toast('Não foi possível executar.', {
          color: 'red',
          icon: 'error',
          dismissable: true,
          queueable: true
        })
        this.dialogNote[id] = false
        console.log(e)
      })
    },

    update() {
      this.dataLoading = true
      this.$store.dispatch('getTasks')
      .then(() => {
        this.tasks = this.$store.getters.getTasks
        this.dataLoading = false
      })
    },

    view(id) {
      this.$router.push({ name: 'ViewTask', params: { taskId: id } })
    },

    create() {
      this.$router.push({ name: 'NewTask'})
    },

    edit(id) {
      this.$router.push({ name: 'EditTask', params: { taskId: id } })
    },

    async confirmDeletion(id) {
      const res = await this.$dialog.warning({
        text: 'Você tem certeza disso? Esta decisão não pode ser desfeita.',
        title: 'Remover'
      })
      if (res){
        this.remove(id)
      }
    },
    
    remove(id) {      
      this.$store.dispatch('deleteTask', id)
      .then(() => {
        // this.tasks.splice(this.tasks.findIndex(e=> e.id == id),1)

        this.$toast('Registro removido.', {
          color: 'green',
          icon: 'check_circle',
          dismissable: true,
          queueable: true
        })
      })
      .catch(e => {
        this.$toast('Não foi possível remover.', {
          color: 'red',
          icon: 'error',
          dismissable: true,
          queueable: true
        })
        console.log(e)
      })
    }
  }
}
</script>
