/* eslint-disable vue/name-property-casing */
<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex sm6>
          <v-card>
            <v-card-title
              ><h3>Título: {{ project.name }}</h3></v-card-title
            >
            <v-card-title
              ><h3>Dono: {{ project.owner }}</h3></v-card-title
            >
            <v-card-title class="card-title">Descrição</v-card-title>
            <v-card-text class="card-description-text"
              ><h4>{{ project.description ? project.description : "Projeto não tem descrição" }}</h4></v-card-text
            >
          </v-card>
        </v-flex>
        <v-flex v-if="project.plans" sm6>
          <v-dialog v-model="dialog">
            <template v-slot:activator="{ on }">
              <v-card>
                <div v-for="(plan, index) in project.plans" :key="'plan-' + index">
                  <v-card>
                    <v-card-title @click="showModalData(plan)" v-on="on" class="card-description-text"
                      >Mapa: &nbsp <a>{{ `${plan.name} - ${plan._id.substr(plan._id.length - 5)}` }}</a>
                    </v-card-title>
                  </v-card>
                </div>
              </v-card>
            </template>

            <v-card>
              <plan-details :plan="clickedPlan[clickedPlan.length - 1]"></plan-details>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" text @click=";(dialog = false), (closeModalData = true)">
                  Sair
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import PlanDetails from "../plan/PlanDetails"
export default {
  // eslint-disable-next-line vue/name-property-casing
  name: "project-details",
  props: {
    project: {
      type: Object,
      default: null
    }
  },
  components: { PlanDetails },

  data() {
    return {
      dialog: false,
      showData: false,
      clickedPlan: [],
      plan: {
        id: null,
        album: "",
        location: "",
        owner: "",
        project: ""
      }
    }
  },
  methods: {
    showModalData(plan) {
      this.showData = true
      if (plan.owner.fullName) {
        plan.owner = plan.owner.fullName
      }
      this.clickedPlan.push(plan)
    },
    closeModalData() {
      ;(this.showModalData = false), (this.dialog = false)
      this.clickedPlan.pop()
    }
  }
}
</script>

<style lang="scss">
.card-plans-style {
  text-align: center;
  padding: 1%;
  margin: 1%;
}
.card-title {
  font-size: 2rem;
}
.card-description-text {
  font-size: 1.3rem;
}
</style>
