/* eslint-disable vue/name-property-casing */
<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card class="card-info-style">
            <v-card-title class="card-title">Ortomosaico</v-card-title>

            <v-card-text> Tipo: {{ orthomosaic.type }}</v-card-text>
            <div v-if="orthomosaic.image">
              <router-link :to="`/image/${orthomosaic.image._id}`">
                <v-btn color="primary" dark> Visualizar Imagem </v-btn>
              </router-link>
            </div>
            <v-card-text> </v-card-text>
          </v-card>
        </v-flex>
        <v-flex sm12
          ><v-dialog v-model="dialog">
            <template v-slot:activator="{ on }">
              <v-btn color="primary" @click="get(orthomosaic.plan._id)" dark v-on="on">Visualizar Mapa</v-btn>
            </template>

            <v-card>
              <plan-details :plan="this.plan"></plan-details>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" flat @click="dialog = false">
                  Sair
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import PlanDetails from "../plan/PlanDetails"
export default {
  // eslint-disable-next-line vue/name-property-casing
  name: "orthomosaic-details",
  props: {
    orthomosaic: {
      type: Object,
      default: null
    }
  },
  components: { PlanDetails },
  data() {
    return {
      dialog: false,
      plan: {
        id: null,
        album: "",
        location: "",
        owner: "",
        project: ""
      }
    }
  },

  methods: {
    get(id) {
      this.$store.dispatch("getPlan", id).then(plan => {
        this.plan = plan
        if (plan.owner.fullName) {
          this.plan.owner = plan.owner.fullName
        } else {
          this.plan.owner = plan.owner
        }
      })
    }
  }
}
</script>

<style lang="scss">
.card-info-style {
  text-align: center;
  padding: 1%;
  margin: 1%;
}
.card-title {
  font-size: 2rem;
}
.card-description-text {
  font-size: 1.3rem;
}
</style>
