<template>
  <div><project-details :project="project"></project-details></div>
</template>

<script>
import ProjectDetails from "./ProjectDetails"
export default {
  components: {
    ProjectDetails
  },
  props: {
    projectId: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      project: {
        id: null,
        name: "",
        description: "",
        location: "",
        owner: null
      }
    }
  },
  mounted() {
    if (this.projectId) {
      this.get(this.projectId)
    }
  },
  computed: {},
  methods: {
    get(id) {
      this.$store.dispatch("getProject", id).then(project => {
        this.project = project
        this.project.owner = this.project.owner.fullName
      })
    }
  }
}
</script>
