<template>
  <div id="page-text-fields">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-container>
              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Dono</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listUsers"
                    v-model="plan.owner"
                    label="Selecione"
                    v-validate="'required'"
                    @change="onChangeUser"
                    :error-messages="errors.collect('owner')"
                    item-text="fullName"
                    item-value="id"
                    data-vv-name="owner"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="plan.owner">
                <v-flex xs4>
                  <v-subheader>Nome</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="plan.name"
                    v-validate="'required'"
                    :error-messages="errors.collect('name')"
                    label="Nome"
                    data-vv-name="name"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row v-if="plan.owner">
                <v-flex xs4>
                  <v-subheader>Album</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listAlbums"
                    v-model="plan.album"
                    label="Selecione"
                    :error-messages="errors.collect('album')"
                    item-text="id"
                    item-value="id"
                    data-vv-name="album"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="plan.owner">
                <v-flex xs4>
                  <v-subheader>Projeto</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listProjects"
                    v-model="plan.project"
                    label="Selecione"
                    :error-messages="errors.collect('project')"
                    item-text="id"
                    item-value="id"
                    data-vv-name="project"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-spacer></v-spacer>

                <v-btn @click="clear">Limpar</v-btn>
                <v-btn class="mr-4" @click="validateForm" color="primary">Salvar</v-btn>
              </v-layout>
                
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
  
<script>

export default {
  props: {
    planId: {
      type: String,
      default: null
    },
  },
  data() {
    return {
      plan: {
        id: null,
        name: '',
        location: '',
        owner: null,
        album: null,
        project: null
      },
      listUsers: [],
      listAlbums: [],
      listProjects: [],
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })

    if (this.planId){
      this.update(this.planId)
    }
  },
  computed: {},
  methods: {
    onChangeUser: function () {
      this.$store.dispatch('getProjects', 'owner=' + this.plan.owner)
      .then(() => {
        this.listProjects = this.$store.getters.getProjects
      })
      this.$store.dispatch('getAlbums', 'owner=' + this.plan.owner)
      .then(() => {
        this.listAlbums = this.$store.getters.getAlbums
      })
    },

    update(id) {
      this.$store.dispatch('getPlan', id)
      .then((plan) => {
        this.plan = plan
        this.plan.owner = this.plan.owner.id
        this.onChangeUser()

      })
    },

    validateForm() {
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.save()
        }
      })
    },

    save() {
     if (this.plan.id == null) {
        this.$store.dispatch('postPlan', this.plan)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro adicionado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível adicionar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      } else {
        this.$store.dispatch('editPlan', this.plan)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro editado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível editar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      }
    },

    clear() {
      this.plan.filename = ''
      this.plan.size = ''
      this.plan.resolution = 0
      this.plan.width = 0
      this.plan.height = 0
      this.plan.type = 0
      this.plan.url = ''
      this.plan.location = 0
      this.plan.thumbnail_url = ''
      this.plan.album = null
    }
  }
}
</script>
