<template>
  <div>
    <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-card-title style="font-size:1.4rem">
              Usuário: {{ user.fullName ? user.fullName : user.name }}
            </v-card-title>
            <v-card-text class="text-style-description"
              >Telefone: {{ user.phone ? user.phone.number : "Telefone não cadastrado" }}</v-card-text
            >
            <v-card-text class="text-style-description">
              Email:
              {{ user.email ? user.email : "Email não cadastrado" }}
            </v-card-text>
            <v-card-text v-if="user.address" class="text-style-description">
              Endereço:
              {{ user.address.address1 ? user.address.address1 : "Logradouro não cadastrado" }} ||
              {{ user.address.address2 ? user.address.address2 : "" }} ||
              {{ user.address.houseNumber ? `Número: ${user.address.houseNumber}` : "Número não cadastrado" }} ||
              {{ user.address.zipCode ? `CEP: ${user.address.zipCode}` : "CEP não cadastrado" }} ||
              {{ user.address.city ? `Cidade: ${user.address.city}` : "Cidade não cadastrada" }} ||
              {{ user.address.state ? `Estado: ${user.address.state}` : "Estado não cadastrado" }}
            </v-card-text>
            <v-card-text v-else class="text-style-description">Endereço não cadastrado</v-card-text>
            <plans-expansion-list :plans="plansList"></plans-expansion-list>
            <projects-expansion-list :projects="projectsList"></projects-expansion-list>
            <orthomosaics-expansion-list :orthomosaics="orthomosaicsList"></orthomosaics-expansion-list>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import OrthomosaicsExpansionList from "./OrthomosaicsExpansionList"
import PlansExpansionList from "./PlansExpansionList"
import ProjectsExpansionList from "./ProjectsExpansionList"

export default {
  components: { PlansExpansionList, ProjectsExpansionList, OrthomosaicsExpansionList },
  props: {
    userId: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      clickedPlan: [],
      showPlan: false,

      user: {
        email: "",
        fullName: "",
        firstName: "",
        lastName: "",
        subscriptionPlan: "",
        isSuperAdmin: "",
        phone: "",
        _id: ""
      },
      plansList: [],
      projectsList: [],
      orthomosaicsList: []
    }
  },
  beforeMount() {},
  mounted() {
    if (this.userId) {
      this.get(this.userId)
      this.getPlans(`owner=${this.userId}`)
      this.getProjects(`owner=${this.userId}`)
      this.getOrthomosaics(`owner=${this.userId}`)
    }
  },
  computed: {},
  methods: {
    get(id) {
      this.$store.dispatch("getUser", id).then(user => {
        this.user = user
        console.log(user)
      })
    },
    getPlans(filter) {
      this.$store.dispatch("getPlans", filter).then(plans => {
        this.plansList = plans
      })
    },

    getProjects(filter) {
      this.$store.dispatch("getProjects", filter).then(projects => {
        this.projectsList = projects
      })
    },
    getOrthomosaics(filter) {
      this.$store.dispatch("getOrthomosaics", filter).then(orthomosaics => {
        this.orthomosaicsList = orthomosaics
      })
    },
    getSharings(id) {},
    showModalData(plan) {
      this.showData = true
      if (plan.owner.fullName) {
        plan.owner = plan.owner.fullName
      }
      this.clickedPlan.push(plan)
    },
    closeModalData() {
      ;(this.showModalData = false), (this.dialog = false)
      this.clickedPlan.pop()
    },
    showPlanDetails() {
      this.showPlan = true
    }
  }
}
</script>
<style lang="scss">
.text-style-description {
  font-size: 1.2rem;
  color: #6200ea;
}
</style>
