<template>
  <v-expansion-panel>
    <v-expansion-panel-content>
      <template v-slot:header>
        <div>Visualizar Ortomosaicos ({{ orthomosaics.length }})</div>
      </template>
      <v-card>
        <v-flex sm12>
          <v-dialog v-model="dialog">
            <template v-slot:activator="{ on }">
              <v-card>
                <div v-for="(orthomosaic, index) in orthomosaics" :key="'orthomosaic-' + index">
                  <v-card>
                    <v-card-title @click="showModalData(orthomosaic)" v-on="on" class="card-description-text"
                      >Ortomosaico: &nbsp
                      <a>{{ `${orthomosaic._id.substr(orthomosaic._id.length - 5)}` }}</a>
                    </v-card-title>
                  </v-card>
                </div>
              </v-card>
            </template>

            <v-card>
              <orthomosaic-details
                :orthomosaic="clickedOrthomosaic[clickedOrthomosaic.length - 1]"
              ></orthomosaic-details>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" text @click=";(dialog = false), (closeModalData = true)">
                  Sair
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-card>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>
<script>
import OrthomosaicDetails from "../orthomosaic/OrthomosaicDetails"

export default {
  // eslint-disable-next-line vue/name-property-casing
  name: "orthomosaic-expansion-list",
  props: {
    orthomosaics: {
      type: Array,
      default: null
    }
  },
  components: { OrthomosaicDetails },

  data() {
    return {
      clickedOrthomosaic: [],
      showData: false,
      dialog: false
    }
  },
  mounted() {
    if (this.userId) {
      this.get(this.userId)
    }
  },
  computed: {},
  methods: {
    showModalData(orthomosaic) {
      this.showData = true
      if (orthomosaic.owner.fullName) {
        orthomosaic.owner = orthomosaic.owner.fullName
      }
      this.clickedOrthomosaic.push(orthomosaic)
    },
    closeModalData() {
      ;(this.showModalData = false), (this.dialog = false)
      this.clickedOrthomosaic.pop()
    },
    showOrthomosaicDetails() {
      this.showOrthomosaic = true
    }
  }
}
</script>
