<template>
  <div id="page-text-fields">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-container>
              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Dono</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-autocomplete
                    :items="listUsers"
                    v-model="share.owner"
                    item-text="fullName"
                    item-value="id"
                    data-vv-name="owner"
                    single-line
                  >
                    <v-select
                      :items="listUsers"
                      v-model="share.owner"
                      label="Selecione"
                      v-validate="'required'"
                      :error-messages="errors.collect('owner')"
                      item-text="fullName"
                      item-value="id"
                      data-vv-name="owner"
                      single-line
                    ></v-select>
                  </v-autocomplete>
                </v-flex>
              </v-layout>

              <v-layout row v-if="share.owner">
                <v-flex xs4>
                  <v-subheader>Compartilhamento por link</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-switch
                    :label="share.linkSharing ? 'Sim' : 'Não'"
                    v-model="share.linkSharing"
                    color="success"
                  ></v-switch>
                </v-flex>
              </v-layout>

              <v-layout row v-if="share.owner && !share.linkSharing">
                <v-flex xs4>
                  <v-subheader>Compartilhar com</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-autocomplete
                    :items="listUsers"
                    v-model="share.user"
                    item-text="fullName"
                    item-value="id"
                    data-vv-name="user"
                    single-line
                  >
                    <v-select
                      :items="listUsers"
                      v-model="share.user"
                      label="Selecione"
                      v-validate="'required'"
                      :error-messages="errors.collect('user')"
                      item-text="fullName"
                      item-value="id"
                      data-vv-name="user"
                      single-line
                    ></v-select>
                  </v-autocomplete>
                </v-flex>
              </v-layout>

              <v-layout row v-if="share.owner">
                <v-flex xs4>
                  <v-subheader>Tipo</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    label="Tipo"
                    :items="['Projeto', 'Mapa', 'Album']"
                    @change="onChangeShareObjectType"
                    v-model="shareType"
                    v-validate="'required'"
                    data-vv-name="shareType"
                    :error-messages="errors.collect('shareType')"
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="share.owner && shareType == 'Projeto'">
                <v-flex xs4>
                  <v-subheader>Projeto</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    label="Projeto"
                    :items="listProjects"
                    v-model="share.project"
                    item-text="name"
                    item-value="id"
                    data-vv-name="project"
                    :error-messages="errors.collect('project')"
                  >
                    <template slot="item" slot-scope="{ item }">
                      {{ item.name }} - {{ item._id.substr(item._id.length - 5) }}
                    </template>
                  </v-select>
                </v-flex>
              </v-layout>

              <v-layout row v-if="share.owner && shareType == 'Mapa'">
                <v-flex xs4>
                  <v-subheader>Mapa</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    label="Mapa"
                    :items="listPlans"
                    v-model="share.plan"
                    item-text="name"
                    item-value="id"
                    data-vv-name="plan"
                    :error-messages="errors.collect('plan')"
                  >
                    <template slot="item" slot-scope="{ item }">
                      {{ item.name }} - {{ item._id.substr(item._id.length - 5) }}
                    </template></v-select
                  >
                </v-flex>
              </v-layout>

              <v-layout row v-if="share.owner && shareType == 'Album'">
                <v-flex xs4>
                  <v-subheader>Album</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    label="Album"
                    :items="listAlbums"
                    v-model="share.album"
                    item-text="name"
                    item-value="id"
                    data-vv-name="album"
                    :error-messages="errors.collect('album')"
                  >
                    <template slot="item" slot-scope="{ item }">
                      {{ item.name }} - {{ item._id.substr(item._id.length - 5) }}
                    </template>
                  </v-select>
                </v-flex>
              </v-layout>
              <v-layout row v-if="share.owner">
                <v-flex xs4>
                  <v-subheader>Nível de compartilhamento</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="listLevels"
                    v-model="share.level"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('level')"
                    item-text="name"
                    item-value="level"
                    data-vv-name="level"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-spacer></v-spacer>
                <v-btn @click="clear">Limpar</v-btn>
                <v-btn class="mr-4" @click="validateForm" color="primary">Salvar</v-btn>
              </v-layout>
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  props: {
    shareId: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      shareType: "",
      listLevels: [
        { level: 1, name: "Vizualizar" },
        { level: 2, name: "Vizualizar + Editar" },
        { level: 3, name: "Vizualizar + Editar + Compartilhar" }
      ],

      share: {
        id: null,
        level: 1,
        linkSharing: false,
        owner: null,
        project: null,
        plan: null,
        album: null,
        user: null
      },
      listUsers: [],
      listProjects: [],
      listPlans: [],
      listAlbums: []
    }
  },
  mounted() {
    this.$store.dispatch("getUsers").then(() => {
      this.listUsers = this.$store.getters.getUsers
    })

    if (this.shareId) {
      this.update(this.shareId)
    }
  },
  computed: {},
  methods: {
    onChangeShareObjectType: function() {
      if (this.shareType == "Projeto") {
        this.$store.dispatch("getProjects", "owner=" + this.share.owner).then(() => {
          this.listProjects = this.$store.getters.getProjects
        })
      }
      if (this.shareType == "Mapa") {
        this.$store.dispatch("getPlans", "owner=" + this.share.owner).then(() => {
          this.listPlans = this.$store.getters.getPlans
        })
      }
      if (this.shareType == "Album") {
        this.$store.dispatch("getAlbums", "owner=" + this.share.owner).then(() => {
          this.listAlbums = this.$store.getters.getAlbums
        })
      }
    },

    update(id) {
      this.$store.dispatch("getShare", id).then(share => {
        this.share = share
        this.share.owner = this.share.owner.id
        if (this.share.project) {
          this.share.project = this.share.project.id
          this.shareType = "Projeto"
        }
        if (this.share.plan) {
          this.share.plan = this.share.plan.id
          this.shareType = "Mapa"
        }
        if (this.share.album) {
          this.share.album = this.share.album.id
          this.shareType = "Album"
        }
        this.onChangeShareObjectType()
      })
    },

    validateForm() {
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.save()
        }
      })
    },

    save() {
      if (this.share.id == null) {
        this.$store
          .dispatch("postShare", this.share)
          .then(() => {
            this.$router.go(-1)
            this.$toast("Registro adicionado.", {
              color: "green",
              icon: "check_circle",
              dismissable: true,
              queueable: true
            })
          })
          .catch(e => {
            this.$toast("Não foi possível adicionar.", {
              color: "red",
              icon: "error",
              dismissable: true,
              queueable: true
            })
            console.log(e)
          })
      } else {
        this.$store
          .dispatch("editShare", this.share)
          .then(() => {
            this.$router.go(-1)
            this.$toast("Registro editado.", {
              color: "green",
              icon: "check_circle",
              dismissable: true,
              queueable: true
            })
          })
          .catch(e => {
            this.$toast("Não foi possível editar.", {
              color: "red",
              icon: "error",
              dismissable: true,
              queueable: true
            })
            console.log(e)
          })
      }
    },

    clear() {
      this.share.level = 1
      this.share.linkSharing = false
      this.share.owner = null
      this.share.project = null
      this.share.plan = null
      this.share.album = null
      this.share.user = null
    }
  }
}
</script>
