/* eslint-disable vue/name-property-casing */
<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex sm6>
          <v-card class="card-info-image">
            <v-card-title class="card-title">Arquivo</v-card-title>
            <v-card
              v-if="this.image.url"
              class="mx-auto"
              :flat="flat"
              :loading="loading"
              :outlined="outlined"
              :elevation="elevation"
              :raised="raised"
              :width="width"
              :height="height"
            >
              <v-img class="white--text" height="200px" :src="image.url">
                <v-card-title class="align-end fill-height">{{ image.filename }}</v-card-title>
              </v-img>
            </v-card>
          </v-card>
        </v-flex>
        <v-flex sm6>
          <v-card class="card-info-style">
            <v-card-title class="card-title">Localização</v-card-title>
            <v-card-text
              >Altitude:
              {{ image.location.altitude ? image.location.altitude : "Imagem não tem altitude definida" }}</v-card-text
            >
            <v-card-text
              >Longitude:
              {{
                image.location.longitude ? image.location.longitude : "Imagem não tem longitude definida"
              }}</v-card-text
            >
            <v-card-text
              >Latitude:
              {{ image.location.latitude ? image.location.latitude : "Imagem não tem latitude definida" }}</v-card-text
            >
          </v-card>
        </v-flex>
        <v-flex sm6>
          <v-card>
            <v-card-title class="card-title">Informações</v-card-title>
            <v-card-text>Largura: {{ image.width ? image.width : "Imagem não tem largura definida" }}px</v-card-text>
            <v-card-text>Altura: {{ image.height ? image.height : "Imagem não tem altura definida" }}px</v-card-text>
            <v-card-text>Tipo: {{ image.type ? image.type : "Imagem não tem tipo definido" }}</v-card-text>
            <v-card-text
              >Resolução: {{ image.resolution ? image.resolution : "Imagem não tem resolução definida" }}</v-card-text
            >
            <v-card-text>Tamanho: {{ image.size ? image.size : "Imagem não tem tamanho definido" }}Mb</v-card-text>
          </v-card>
        </v-flex>
        <v-flex sm6 :v-if="image.album">
          <v-dialog v-model="dialog">
            <template v-slot:activator="{ on }">
              <v-card>
                <v-btn @click="get(image.album._id)" v-on="on" class="card-description-text"
                  >Álbum: &nbsp <a>{{ `${image.album.name}` }}</a>
                </v-btn>
              </v-card>
            </template>
            <v-card>
              <v-btn color="primary" text @click="dialog = false">
                Sair
              </v-btn>
            </v-card>
            <v-card v-if="this.image.album">
              <album-details :album="this.album"></album-details>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import AlbumDetails from "../album/AlbumDetails"
export default {
  // eslint-disable-next-line vue/name-property-casing
  name: "image-details",
  props: {
    image: {
      type: Object,
      default: null
    }
  },
  components: { AlbumDetails },
  data() {
    return {
      dialog: false,
      listUsers: [],
      album: {
        id: null,
        name: "",
        description: "",
        altitude: 0,
        width: 0,
        camera: 0,
        height: 0,
        type: "",
        count: 0,
        owner: null
      }
    }
  },
  methods: {
    get(id) {
      this.$store.dispatch("getAlbum", id).then(album => {
        console.log("hELLOWORLD")
        this.album = album
        if (album.owner.fullName) {
          this.album.owner = album.owner.fullName
        } else {
          this.album.owner = album.owner
        }
      })
    }
  }
}
</script>

<style lang="scss">
