//   padding: 1%;
//   margin: 1%;
// }

// .card-info-style {
//   text-align: center;
//   padding: 1%;
//   margin: 1%;
//   height: 20rem;
// }
.card-title {
  font-size: 2rem;
}
.card-description-text {
  font-size: 1.3rem;
}
</style>
