<template>
  <div id="page-text-fields">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          <v-card>
            <v-container>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>E-mail</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="user.email"
                    v-validate="'required|email'"
                    :error-messages="errors.collect('email')"
                    label="E-mail"
                    data-vv-name="email"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Nome</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="user.name"
                    v-validate="'required'"
                    :error-messages="errors.collect('name')"
                    label="Nome"
                    data-vv-name="name"
                    required
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Tipo</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="['unconfirmed', 'confirmed']"
                    v-model="user.emailStatus"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('emailStatus')"
                    data-vv-name="emailStatus"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Plano</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-select
                    :items="['free', 'monthly', 'anual', 'premium']"
                    v-model="user.subscriptionPlan"
                    label="Selecione"
                    v-validate="'required'"
                    :error-messages="errors.collect('subscriptionPlan')"
                    data-vv-name="subscriptionPlan"
                    single-line
                  ></v-select>
                </v-flex>
              </v-layout>
              
              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Super-Administrador</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-switch :label="user.isSuperAdmin ? 'Sim' : 'Não'" v-model="user.isSuperAdmin" color="success"></v-switch>
                </v-flex>
              </v-layout>
              
              <v-layout row>
                <v-flex xs4>
                  <v-subheader>Definir senha</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-switch :label="setPassword ? 'Sim' : 'Não'" v-model="setPassword" color="success"></v-switch>
                </v-flex>
              </v-layout>

              <v-layout row v-if="setPassword">
                <v-flex xs4>
                  <v-subheader>Senha</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="user.password"
                    :append-icon="show1 ? 'visibility' : 'visibility_off'"
                    :type="show1 ? 'text' : 'password'"
                    v-validate="'min:8|verify_password'"
                    :error-messages="errors.collect('password')"
                    label="Senha"
                    hint="Senha de 8 caracteres com MAIÚSCULAS, minúsculas, números e símbolos"
                    data-vv-name="password"
                    counter
                    @click:append="show1 = !show1"
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row v-if="setPassword">
                <v-flex xs4>
                  <v-subheader>Confirmação da senha</v-subheader>
                </v-flex>
                <v-flex xs8>
                  <v-text-field
                    v-model="user.passwordConfirmation"
                    :append-icon="show2 ? 'visibility' : 'visibility_off'"
                    :type="show2 ? 'text' : 'password'"
                    v-validate="'min:8|verify_password'"
                    :error-messages="errors.collect('passwordConfirmation')"
                    label="Confirmação de Senha"
                    hint="Senha de 8 caracteres com MAIÚSCULAS, minúsculas, números e símbolos"
                    data-vv-name="passwordConfirmation"
                    counter
                    @click:append="show2 = !show2"
                  ></v-text-field>
                </v-flex>
              </v-layout>

              <v-layout row>
                <v-spacer></v-spacer>

                <v-btn @click="clear">Limpar</v-btn>
                <v-btn class="mr-4" @click="validateForm" color="primary">Salvar</v-btn>
              </v-layout>
                
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
  
<script>

export default {
  props: {
    userId: {
      type: String,
      default: null
    },
  },
  data() {
    return {
      show1: false,
      show2: false,
      setPassword: true,
      user: {
        id: null,
        email: "",
        name: "",
        isSuperAdmin: false,
        emailStatus: "unconfirmed",
        phone: null,
        subscriptionPlan: "free",
        password: "",
        passwordConfirmation: ""
      },
      listUsers: []
    }
  },
  mounted () {
    if (this.userId){
      this.update(this.userId)
    }
  },
  computed: {},
  methods: {
    update(id) {
      this.$store.dispatch('getUser', id)
      .then((user) => {
        this.user = user
        this.user.name = user.fullName
        this.setPassword = false
      })
    },

    validateForm() {
      this.$validator.validateAll().then(async result => {
        if (result) {
          this.save()
        }
      })
    },

    save() {
     if (this.user.id == null) {
        this.$store.dispatch('postUser', this.user)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro adicionado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível adicionar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      } else {
        this.$store.dispatch('editUser', this.user)
        .then(() => {
          this.$router.go(-1)
          this.$toast('Registro editado.', {
            color: 'green',
            icon: 'check_circle',
            dismissable: true,
            queueable: true
          })
        }).catch(e => {
          this.$toast('Não foi possível editar.', {
            color: 'red',
            icon: 'error',
            dismissable: true,
            queueable: true
          })
          console.log(e)
        })
      }
    },

    clear() {
      this.user.email = ""
      this.user.name = ""
      this.user.isSuperAdmin = false
      this.user.emailStatus = "unconfirmed"
      this.user.phone = null
      this.user.subscriptionPlan = "free"
      this.user.password = ""
      this.user.passwordConfirmation = ""
    }
  }
}
</script>
